<template>
  <div id="me">
    <header>
      <img src="../../assets/me/list@3x.png" alt="" @click="showSide">
      <ul>
        <li @click="tabActive(1)">
          Device
          <div class="line" v-if="active === 1"></div>
        </li>
        <li @click="tabActive(2)">
          Room
          <div class="line" v-if="active === 2"></div>
        </li>
      </ul>
    </header>
    <div class="content" v-show="active === 1">
      <div class="full" v-if="false">
        <img src="../../assets/me/logo@3x.png" alt="">
        <h3>Wellcome to growroomate</h3>
        <p>Please add a roomate smart device clicking below</p>
      </div>
      <div class="list" v-if="true">
        <ul>
          <li class="list_li" v-for=" item in deviceData" :key="item.id"
            @touchstart="chooseItem($event, 'start', item)"
            @touchmove="chooseItem($event, 'move', item)"
            @touchend="chooseItem($event, 'end', item)">
            <div class="left">
              <img :src="item.icon" alt="">
              <span>{{item.device}}</span>
            </div>
            <div class="right">
              <span>{{item.status}}</span>
              <img src="../../assets/me/next@3x.png" alt="">
            </div>
            <div class="side">
              <div class="rename" @click="rename()">
                Rename
              </div>
              <div class="delate" @click="remove">
                <span>Delete</span>
              </div>
            </div>
          </li>
        </ul>
        <span class="options">Press on your device to view</span>
      </div>
      <div class="button" @click="addDevice(1)">
        Add Devices
      </div>
    </div>
    <!-- ---------------------------------------------------active ---2 ---------------------------------------->
    <div class="content_2" v-show="active === 2">
      <div class="full" v-if="!true">
        <img src="../../assets/me/logo@3x.png" alt="">
        <h3>Group devices into rooms for easier managment
of multi-room control</h3>
        <p>Please add a room by clicking below</p>
      </div>
      <div class="list" v-if="!false">
        <ul>
          <li class="list_li" v-for="item in roomsData" :key="item.id"
            @touchstart="chooseItem($event, 'start')"
            @touchmove="chooseItem($event, 'move')"
            @touchend="chooseItem($event, 'end', item)">
            <div class="left">
              <span>{{item.room}}</span>
            </div>
            <div class="right">
              <img alt="" :src="icon" v-for="icon in item.icon" :key="icon.id">
            </div>
            <div class="side">
              <div class="rename" @click="rename">
                <span>Rename</span>
              </div>
              <div class="delate" @click="remove">
                <span>Delete</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="button" @click="addDevice(2)">
        Add Rooms
      </div>
    </div>
    <side-bar></side-bar>
    <!-- Updates -->
    <messageBox setHeight="190" setType="confirm" v-show="messageBoxType === 'showUpdate'" sideMessage="true">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>new version update</span>
        </div>
        <div class="alert_describe">The content of new version update</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(0)" style="background:#cccccc;">Cancle</div>
          <div class="determine_one" @click="alertChoose(1)" style="background:#13af68;">OK</div>
        </div>
      </div>
    </messageBox>
    <!-- logout -->
    <messageBox setHeight="190" setType="confirm" v-show="messageBoxType === 'showLogout'" sideMessage="true">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>Logout</span>
        </div>
        <div class="alert_describe">Are you sure to logout</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(2)" style="background:#cccccc;">Cancle</div>
          <div class="determine_one" @click="alertChoose(3)" style="background:#13af68;">Yes</div>
        </div>
      </div>
    </messageBox>
    <!-- Rename -->
    <messageBox setHeight="235" setType="confirm" marginTop="-60%" v-show="messageBoxType === 'Rename'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>Rename</span>
        </div>
        <div class="rename">
          <input type="text" v-model="renameVal" @input="nameExp(renameVal)">
          <img src="../../assets/me/del@3x.png" alt="" @click="clearVal" v-show="renameVal !==''">
        </div>
        <div class="describe" :style="{color:renameExp? '': 'red'}">Name must between 2-16 characters long, only letters and numbers allowed</div>
        <div class="alert_operation">
          <div class="determine_two" @click="alertChoose(0)" style="background:#cccccc;">Cancle</div>
          <div class="determine_two" @click="alertChoose(4)" :style="{background:renameVal ? '#13af68': '#cccccc'}">Yes</div>
        </div>
      </div>
    </messageBox>
    <!-- Remove -->
    <messageBox setHeight="190" setType="confirm" marginTop="-60%" v-show="messageBoxType === 'Remove'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>{{removeType}}</span>
        </div>
        <div class="alert_describe">Are you sure to remove it?</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(4)" style="background:#cccccc;">Cancle</div>
          <div class="determine_one" @click="alertChoose(7)" style="background:#13af68;">Yes</div>
        </div>
      </div>
    </messageBox>
    <!-- toast -->
    <div class="messageToast" v-if="animFlag || removeSuccess || removeFailed" >
      <div v-if="animFlag">
        <img src="../../assets/device/loading@3x.png" alt="" :class="animFlag? 'Rotation': ''">
        <span>{{messageToastType}}</span>
      </div>
      <div v-if="!true" >
        <img src="../../assets/device/succeed-icon@3x.png" alt="">
        <span v-if="active === 1">Remove the device successfully</span>
        <span v-if="active === 2">Remove the device successfully</span>

      </div>
      <div v-if="removeFailed">
        <img src="../../assets/device/wrong@3x.png" alt="">
        <span style="marginTop:10px" v-if="active === 1">Failed to remove the device,please retry</span>
        <span style="marginTop:10px" v-if="active === 2">Failed to remove the room,please retry</span>

      </div>
    </div>
    <transition mode="in-out" name="slide-left">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import SideBar from '../../components/common/sideBar.vue'
import headTop from '../../components/common/head'
import messageBox from '../../components/common/messageBox'

const heartIMG = require('../../assets/device/heater@3x.png')
const humideyIMG = require('../../assets/device/humidier@3x.png')
const lightIMG = require('../../assets/device/light@3x.png')
export default {
  components: {
    'side-bar': SideBar,
    'head-top': headTop,
    messageBox
  },
  data () {
    return {
      active: 1, // 当前菜单
      flagdelter: true, // 左滑
      startX: null, // 水平移动距离
      startY: null, // 垂直移动距离
      delterflag: false, // true 左滑按钮显示
      endX: null,
      Rename: false, // 重命名弹窗
      renameVal: ' Rename',
      renameExp: true,
      Remove: false,
      animFlag: false, // 动画时间5s
      removeSuccess: false, // 移除成功
      removeFailed: false, // 移除失败
      removeType: null, // messageBox remove type device rooms
      messageToastType: null, // messageToast remove type device rooms
      liNode: null, // 当前页面的li
      deviceData: [], // device list
      roomsData: [] // rooms list
    }
  },
  computed: {
    ...mapGetters(['messageType']),
    showLogout () {
      return this.$store.getters.showLogout
    },
    showUpdate () {
      return this.$store.getters.showUpdate
    },
    messageBoxType () {
      return this.messageType
    }
  },
  mounted () {
    this.deviceData = [
      {
        icon: heartIMG,
        device: 'Heater',
        status: '25%'
      },
      {
        icon: humideyIMG,
        device: 'Humidier',
        status: '50%'
      },
      {
        icon: lightIMG,
        device: 'Light 1',
        status: 'ON'
      },
      {
        icon: lightIMG,
        device: 'Light 2',
        status: 'OFF'
      }
    ]
    this.roomsData = [
      {
        icon: [humideyIMG, heartIMG, lightIMG],
        room: 'Growroom1'
      },
      {
        icon: [humideyIMG, lightIMG],
        room: 'Growroom2'
      },
      {
        icon: [humideyIMG, heartIMG, lightIMG, lightIMG],
        room: 'Growroom3'
      },
      {
        icon: [humideyIMG],
        room: 'Growroom4'
      }
    ]
  },
  methods: {
    ...mapActions(['showMessagesBox', 'roomName', 'addevicePath']),
    showSide () {
      this.$store.dispatch('showSideBar')
      this.$store.dispatch('hideSideBarmask')
    },
    tabActive (num) {
      console.log(this.delterflag)
      if (!this.delterflag) {
        switch (num) {
          case 1:
            this.active = 1
            break
          case 2:
            this.active = 2
            break
          default:
            break
        }
      } else {
        let Slistli = this.liNode
        for (let i = 0; i < Slistli.length; i++) {
          if (parseInt(Slistli[i].style.marginLeft) < 0) {
            this.flagdelter = false
            let x = parseInt(Slistli[i].style.marginLeft)
            let timer = setInterval(() => {
              if (x < 0) {
                x += 10
              } else {
                x = 0
                this.flagdelter = true
                this.delterflag = false
                clearInterval(timer)
              }
              Slistli[i].style.marginLeft = x + 'px'
            }, 30)
          }
        }
      }
    },
    alertChoose (num) {
      switch (num) {
        case 0: // 检查更新
          this.showMessagesBox({type: null})
          break
        case 1:
          this.showMessagesBox({type: null})
          break
        case 2: // 退出
          this.showMessagesBox({type: null})
          break
        case 3:
          this.showMessagesBox({type: null})
          break
        case 4: // rename ok
          if (this.renameVal !== '') {
            this.showMessagesBox({type: null})
          }
          break
        case 5:
          this.Rename = false
          break
        case 6: // 退出
          this.Remove = false
          break
        case 7:
          this.showMessagesBox({type: null})
          this.animFlag = true
          setTimeout(() => {
            this.animFlag = false
            this.removeFailed = true
            setTimeout(() => {
              this.removeFailed = false
              clearTimeout()
            }, 2000)
            clearTimeout()
          }, 5000)
          break
        default:
          break
      }
    },
    addDevice (active) {
      console.log(active)
      if (active === 1) {
        this.addevicePath('/Me')
        this.$router.push({path: '/Me/addevice'})
      } else if (active === 2) {
        this.$router.push({path: '/Me/addRoom'})
      }
    },
    // 左滑
    chooseItem (e, type, item) {
      let Listli = e.target
      while (Listli.className !== 'list_li') {
        Listli = Listli.parentNode
        if (Listli.className === 'side') {
          return false
        }
      }
      let Slistli = document.getElementsByClassName('list_li')
      this.liNode = Slistli
      switch (type) {
        case 'start':
          for (let i = 0; i < Slistli.length; i++) {
            if (parseInt(Slistli[i].style.marginLeft) < 0) {
              this.flagdelter = false
              let x = parseInt(Slistli[i].style.marginLeft)
              let timer = setInterval(() => {
                if (x < 0) {
                  x += 10
                } else {
                  x = 0
                  this.flagdelter = true
                  this.delterflag = false
                  clearInterval(timer)
                }
                Slistli[i].style.marginLeft = x + 'px'
              }, 30)
            }
          }
          // this.isMove = false
          this.startX = e.changedTouches['0'].pageX
          this.startY = e.changedTouches['0'].pageY
          break
        case 'move':
        //   this.isMove = true
          let liLeft = e.changedTouches['0'].pageX - this.startX
          let moveY = e.changedTouches['0'].pageY - this.startY
          console.log(Math.abs(moveY))
          if (liLeft < -35 && this.flagdelter && Math.abs(moveY) < 40) {
            if (liLeft < -120) {
              this.delterflag = true
              Listli.style.marginLeft = -120 + 'px'
            } else if (liLeft > 0) {
              Listli.style.marginLeft = 0 + 'px'
            } else {
              if (liLeft === -120) {
                console.log(2)
                this.delterflag = true
              }
              Listli.style.marginLeft = liLeft + 'px'
            }
          } else {
            for (let i = 0; i < Slistli.length; i++) {
              console.log(Slistli[i].style.marginLeft)
              if (parseInt(Slistli[i].style.marginLeft) < 0) {
                let x = parseInt(Slistli[i].style.marginLeft)
                let timer = setInterval(() => {
                  if (x < 0) {
                    x += 10
                    console.log(x)
                  } else {
                    x = 0
                    this.delterflag = false
                    clearInterval(timer)
                  }
                  Slistli[i].style.marginLeft = x + 'px'
                }, 30)
              }
            }
          }
          // }
          // console.log(e.changedTouches["0"].pageX)
          break
        case 'end':
          liLeft = e.changedTouches['0'].pageX - this.startX
          moveY = e.changedTouches['0'].pageY - this.startY
          if (Math.abs(moveY) < 25) {
            this.endX = e.changedTouches['0'].pageX
            if ((this.startX - this.endX) < 120 && (this.startX - this.endX) > 0) {
              let x = this.endX - this.startX
              let timer = setInterval(() => {
                if (x > -120) {
                  x += -10
                } else {
                  x = -120
                  this.delterflag = true // 删除显示
                  clearInterval(timer)
                }
                Listli.style.marginLeft = x + 'px'
              }, 30)
            }
          }
          console.log(Math.abs(this.endX - this.startX))
          if (Math.abs(this.endX - this.startX) === 0 && !this.delterflag) {
            if (this.active === 1) {
              this.$router.push({'path': '/temperature', query: {'device': item.device}})
            } else if (this.active === 2) {
              this.$router.push({'name': 'rooms', 'params': {'device': item.room}})
              this.roomName(item.room)
            }
          }
          // 判断左滑还是点击
          // Slistli = document.getElementsByClassName('list_li')
          break
      }
    },
    rename () {
      this.showMessagesBox({type: 'Rename'})
    },
    nameExp (val) {
      // 输入内容格式验证
      if (!/^\w{0,16}$/.test(val)) {
        this.renameExp = false
      } else {
        this.renameExp = true
      }
    },
    clearVal () {
      this.renameVal = ''
    },
    remove () {
      if (this.active === 1) {
        this.removeType = 'Remove Devices'
        this.messageToastType = 'Removing the device'
      } else {
        this.messageToastType = 'Removing the room'
        this.removeType = 'Remove Rooms'
      }
      this.showMessagesBox({type: 'Remove'})
    }

  }
}
</script>

<style scoped lang="less">
#me{
  .wh(100%, 100%);
  background: url(../../assets/global/bg-@3x.png);
  background-size: cover;
  position: relative;
  .side_bar{
    .wh(30px, 30px);
    background: yellow;
  }
  header{
    .wh(100%, 100px);
    padding: 0 30px;
    display: flex;
    background: #01a29a;
    img{
      .wh(42px,32px);
      align-self: center;
    }
    ul{
      margin-left: 162px;
      display: flex;
      li{
        .wh(130px, 100px);
        padding-top: 32px;
        color: white;
        font-size: 32px;
        font-weight: 400;
        position: relative;
        .line{
          .wh(74px, 3px);
          margin: 0 auto;
          background: white;
          // margin-top: 18px;
          position: absolute;
          bottom: 10px;
          left: 30px;
        }
      }
    }
  }
  .content, .content_2{
    .wh(100%, 100%);
    display: flex;
    flex-direction: column;
    .full{
      align-self: center;
      display: flex;
      flex-direction: column;
      // height: 63%;
      img{
        .wh(200px, 200px);
        margin-top: 95px;
        align-self: center;
      }
      h3{
        font-size: 34px;
        margin-top: 60px;
      }
      p{
        width: 314px;
        font-size: 23px;
        margin-top: 29px;
        align-self: center;
        color: #898989;
        line-height: 44px;
        align-self: center;
      }
    }
    .list{
      .wh(100%, 100%);
      ul{
        display: flex;
        flex-direction: column;
        margin-top: 30px;
        background: white;
        overflow: hidden;
        li{
          display: flex;
          flex-direction: row;
          // justify-content: space-between;
          .wh(132%, 93px);
          border-top: 1px solid #dadada;
          padding-left: 30px;
          line-height: 93px;
          position: relative;
          .left{
            width: 39%;
            img{
              .wh(23px,38px);
              margin-right: 30px;
              align-self: center;
            }
            display: flex;
            span{
              font-size: 30px;
              color: #575757;
            }
          }
          .right{
            display: flex;
            width: 39%;
            justify-content: flex-end;
            padding-right: 30px;
            span{
              font-size: 23px;
              color: #c4c6c5;
            }
            img{
              .wh(18px,25px);
              align-self: center;
              margin-left: 30px;
            }
          }
          .side{
            .wh(240px, 100%);
            display: flex;
            flex-direction: row;
            font-size: 20px;
            color: #fcfcfc;
            .delate{
              width: 50%;
              background: url(../../assets/device/Deletebuttonfreeicon@3x.png);
              background-size: cover;
              padding-top: 27px;
            }
            .rename{
              width: 50%;
              background: url(../../assets/device/Rename@3x.png);
              background-size: cover;
              position: relative;
              padding-top: 27px;
            }
            // span{
            //   font-size: 15px;
            //   color: #fcfcfc;
            //   // display: inline-block;
            //   vertical-align: baseline
            // }
          }
        }
        li:last-child {
          border-bottom: 1px solid #dadada;
        }
      }
      .options{
        font-size: 26px;
        color: #000;
        margin-top:35px;
      }
    }
    .button{
      .wh-l(520px;105px);
      color: white;
      font-size: 45px;
      background: #13af68;
      border-radius: 10px;
      position: absolute;
      bottom: 18%;
      left: 50%;
      margin-left: -34.6%;
    }
  }
  .common_alert{
    .rename{
      margin: 0 86px;
     .wh-l(438px, 80px);
      display:flex;
      border-bottom: 1px solid #cdcdcd;
      img{
        .wh(24px, 24px);
        display: block;
        align-self: center;
      }
      input{
        .wh(100%,100%);
        font-weight: 900;
        color: #585858;
      }
    }
    .describe{
      .wh(438px, auto);
      margin:  0 auto;
      color: #cccccc;
      font-size: 22px;
      line-height: 30px;
      margin-top: 30px;
    }
  }
  .messageToast{
    display: flex;
    z-index: 10;
    .wh(300px, 260px);
    position: absolute;
    top:50%;
    left: 50%;
    margin: -240px 0 0 -130px;
    background: url(../../assets/device/smallspring@3x.png);
    // box-shadow: 20px 20px 10px 3px #7e7b7b;
    // background: white;
    background-size: cover;
    // border-radius: 5%;
    flex-direction: column;
    div{
    display: flex;
    flex-direction: column;
    .wh(100%,100%);
    }
    img{
      .wh(72px, 72px);
      margin-top: 55px;
      align-self: center;
    }
    .Rotation{
      animation: Rotation 1.5s infinite linear;
    }
    @keyframes Rotation {
      0%{
        transform: rotate(0deg)
      }
      // 20%{
      //   transform: rotate(72deg)
      // }
      // 40%{
      //   transform: rotate(144deg)
      // }
      // 60%{
      //   transform: rotate(216deg)
      // }
      // 80%{
      //   transform: rotate(278deg)
      // }
      100%{
        transform: rotate(360deg)
      }
    }
    span{
      // .wh(160px, auto);
      padding: 0 38px;
      font-size: 25px;
      margin:  0 auto;
      margin-top: 30px;
    }
  }
  .content_2{
    .full{
      h3{
        width: 79%;
        font-weight: 400;
        color: #5d5d5d;
        align-self: center;
        line-height: 67px;
      }
      p{
        width: 54%;
        margin-top: 15px;
      }
    }
    .list ul li{
      img{
        .wh(25px; 40px);
      }
    }
  }
}
</style>



