<template>
  <div class="home">
    <!-- 对transition不了接的可以查看vue2.0官方文档的“过渡 & 动画” 链接：https://cn.vuejs.org/v2/guide/transitions.html-->
    <transition name="fade" @touchmove.stop.prevent>
      <!-- 这部分功能主要是关闭侧栏菜单，类似一层遮罩的效果 -->
      <div class="menu-mask" v-show="hideSideBarmask" @click.stop="hideSide"></div>
    </transition>

    <transition name="slide-fade">
      <!-- 这里才是侧栏代码部分 -->
      <div class="side-content" v-show="isRellyShow">
        <div class="top">
          <img src="../../assets/me/header@3x.png" alt=""  @click="$router.push({path: '/Me/changeProfile'})">
          <div class="name">Tom Cruise</div>
        </div>
        <ul class="nav">
          <li @click="$router.push({path: '/Me/changePassword'})">
            <img src="../../assets/me/Note@3x.png" alt="">
            <span>Change Password</span>
          </li>
          <li @click="$router.push({path: '/Me/currency'})">
            <img src="../../assets/me/currency@3x.png" alt="">
            <span>Currency</span>
            <div class="unit">GBP</div>
          </li>
          <li @click="$router.push({path: '/Me/Rate'})">
            <img src="../../assets/me/rate@3x.png" alt="">
            <span>Rate</span>
            <div class="unit">$0.5</div>
          </li>
          <li @click="update">
            <img src="../../assets/me/Updatearrowsfreeicon@3x.png" alt="">
            <span>Check for Updates</span>
          </li>
          <li @click="$router.push({path: '/Me/aboutus'})">
            <img src="../../assets/me/Avatar@3x.png" alt="">
            <span>About Us</span>
            <div class="update" v-if="updateTip">This is the lateset version</div>
          </li>
          <li @click="logout">
            <img src="../../assets/me/Logout@3x.png" alt="">
            <span>Logout</span>
          </li>
        </ul>
        <div class="bottomlogo">
          <img src="../../assets/me/logo_grey@3x.png" alt="">
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import {mapActions, mapGetters} from 'vuex'
export default {
  components: {},
  data () {
    return {
      updata: !true,
      updateTip: false // 更新提示隐藏
    }
  },
  computed: {
    ...mapGetters(['messageType']),
    isRellyShow () {
      return this.$store.getters.isShowMethod
    },
    hideSideBarmask () {
      return this.$store.getters.hideSideBarmask
    }
  },
  mounted () {
  },
  methods: {
    ...mapActions(['showMessagesBox', 'showMessagesBox']),
    hideSide () {
      if (this.messageType === null) {
        this.$store.dispatch('hideSideBarmask')
        this.$store.dispatch('hideSideBar')
      } else {
        this.showMessagesBox({type: null})
      }
    },
    update () {
      if (this.updata) {
        this.showMessagesBox({type: 'showUpdate'})
      } else {
        this.updateTip = true
        setTimeout(() => {
          this.updateTip = false
          clearTimeout()
        })
      }
    },
    logout () {
      // this.$store.dispatch('hideSideBarmask')
      this.showMessagesBox({type: 'showLogout'})
    }
  }
}
</script>

<style lang="less" scoped>
@import "../../styles/mixin.less";
.home {
  .wh(100%, 100%);
  font-size: 22px;
  color: #585858;
}
.menu-mask {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 1;
  z-index: 10;
  background: rgba(0, 0, 0, 0.5);
}
.side-content {
  z-index: 11;
  position: fixed;
  width: 549px;
  height: 100%;
  background: white;
  top: 0;
  left: 0;
  bottom: 0;
  -webkit-overflow-scrolling: touch;
  .top{
    .wh(100%, auto);
    margin-top: 100px;
    img{
      display: block;
      .wh(220px,220px);
      border-radius: 50%;
      margin:  0 auto;
    }
    .name{
      font-size: 26px;
      .wh(100%, auto);
      margin-top: 40px;
    }
  }
  .nav{
    margin-top: 110px;
    .wh(100%, auto);
    padding: 0 30px;
    li{
      .wh(100%, 80px);
      display: flex;
      span{
        line-height: 80px;
        flex: 1;
        text-align: left;
      }
      img{
        .wh(34px, 29px);
        align-self: center;
        margin-right: 30px;
      }
      .unit{
        align-self: flex-end;
        line-height: 80px;
        font-size: 17px;
        color: #999999;
      }
      .update{
        .wh(290px, 70px);
        align-self: center;
        background: url(../../assets/me/btn-gray@3x.png);
        background-size: cover;
        padding-left: 25px;
        font-size: 25px;
        color: white;
        padding-top: 10px;
      }
    }
  }
}
.fade-enter-to,
.fade-leave-to {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.slide-fade-enter-to,
.slide-fade-leave-to {
  transition: transform 0.3s;
  transform: translate(0px, 0px);
}
.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
  -webkit-transform: translate(-286px, 0px);
  transform: translate(-286px, 0px);
  -webkit-transition: opacity 0.3s ease-in-out 0.3s,
    -webkit-transform 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out 0.3s, transform 0.3s ease-in-out;
}
.change {
  display: block;
  width: 284px;
  height: 200px;
  background: url(http://p3.qhimg.com/t0134c65e59012a1257.png) no-repeat center;
  background-size: cover;
  border: 1px solid #fff;
  overflow: hidden;
}
.change img {
  display: block;
  width: 220px;
  height: 180px;
  opacity: 0;
  -webkit-transform: translate(284px, 200px);
  transform: translate(284px, 200px);
  -webkit-transition: opacity 0.5s ease-in-out 0.5s,
    -webkit-transform 1s ease-in-out;
  transition: opacity 0.5s ease-in-out 0.5s, transform 1s ease-in-out;
}
.change:hover img {
  -webkit-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
  opacity: 1;
}
.bottomlogo{
  margin: 0 auto;

  margin-top: 140px;
  .wh(140px, auto);
  img{
    .wh(140px, 160px);
    opacity: 0.3;
  }
}
</style>



